.App {
  text-align: center;
  background: url("./assets/container_bg.png");
  background-size: 100% 100%;
  min-height: 100vh;
  color: white;
}

.App-header {
  padding: 30px 80px 16px 60px;
}

.App-title {
  font-size: 22px;
  font-weight: bold;
}

.App-slogan {
  border-top: 1.5px solid rgba(255, 255, 255, 0.6);
  border-bottom: 1.5px solid rgba(255, 255, 255, 0.6);
  font-size: 15px;
  font-weight: bold;
  line-height: 24px;
  margin: 12px 0 8px 0;
  padding: 8px 0;
}

.App-tip {
  font-size: 12px;
  opacity: 0.5;
}

.content {
  height: calc(100vh - 204px);
  overflow: auto;
  padding: 0 24px;
  text-align: center;
}
.module {
  height: 220px;
  width: 41%;
  margin: 4%;
  float: left;
  background: url("./assets/module_bg.png");
  background-size: 100% 100%;
  border: 1px dashed rgba(255, 255, 255, 0.5);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.module .module-title {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.module-title span {
  font-size: 16px;
  font-weight: bold;
  border-bottom: 2px solid rgba(255, 255, 255, 0.6);
  font-style: italic;
  margin: 0 0 8px 0;
  padding: 0 0 4px 0;
}
.module .opration {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 4px;
}

.status-icon {
  width: 20px;
  height: 20px;
}
.status-btn {
  height: 20px;
  line-height: 20px;
  min-width: 70px;
  border-radius: 15px;
  font-size: 12px;
  background: rgba(255, 255, 255, 0.6);
  color: #fff;
}
.status-btn::before,
.submit-btn::before {
  border: 0 !important;
}

.submit-btn {
  height: 30px;
  line-height: 30px;
  width: 120px;
  border-radius: 15px;
  font-size: 14px;
  top: 30px;
  margin: 30px auto 80px auto;
}

.status-btn.active {
  background: rgba(255, 255, 255);
  color: #000;
}
.App-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 40px;
  width: 100%;
  line-height: 40px;
  background: #182e49;
  opacity: 0.8;
  font-size: 16px;
  font-weight: bold;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
